@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  width: 100%;
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 768px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 768px) {
    .container--no-padding {
      padding: 0; } }

.animation-change-color {
  animation: changeColor 7s infinite; }

.animation-fade {
  animation: fade 2s ease-in-out; }

@keyframes fade {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes changeColor {
  0% {
    color: #2c39f2; }
  33% {
    color: #ff1553; }
  66% {
    color: #9627a3; }
  100% {
    color: #2c39f2; } }

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: flex-end;
  padding-bottom: 77px;
  font-size: 24px;
  line-height: 22px;
  text-align: center;
  z-index: 110000;
  background-color: #ffffff;
  overflow: hidden; }
  .preloader__logo {
    margin-bottom: 190px; }
    @media (max-width: 949px) {
      .preloader__logo {
        transform: scale(90%); } }
    @media (max-width: 768px) {
      .preloader__logo {
        transform: scale(80%); } }

.header {
  padding: 25px 0;
  z-index: 10000; }
  .header__wrapper {
    display: flex;
    padding: 0 35px; }
  .header__logo {
    display: inline-flex; }
  @media (max-width: 768px) {
    .header {
      background-color: #ffffff; } }

.logo {
  display: inline-flex; }
  .logo__desk {
    color: #2c39f2; }

.main-intro {
  display: flex;
  flex-direction: column;
  height: 100%; }
  .main-intro h2 {
    margin: 0;
    font-weight: normal;
    font-size: 96px;
    line-height: 96px;
    color: #2c39f2; }
    @media (max-width: 949px) {
      .main-intro h2 {
        padding-right: 33%; } }
  .main-intro p {
    display: block;
    margin: 32px 0 15px;
    font-size: 30px;
    line-height: 40px; }
    @media (max-width: 949px) {
      .main-intro p {
        margin-top: 17px; } }
    @media (max-width: 768px) {
      .main-intro p {
        max-width: 350px; } }
  .main-intro__wrapper {
    position: relative;
    display: flex;
    align-items: flex-end;
    margin: auto 0;
    padding: 0 35px; }
    @media (max-width: 949px) {
      .main-intro__wrapper {
        display: block;
        margin: auto 0 0; } }
    @media (max-width: 768px) {
      .main-intro__wrapper {
        margin: auto 0; } }
  .main-intro__bg {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%); }
    .main-intro__bg svg {
      display: block; }
    @media (max-width: 768px) {
      .main-intro__bg {
        display: none; } }
  @media (max-width: 949px) {
    .main-intro__dino {
      position: absolute;
      top: 15%;
      right: 0; } }
  @media (max-width: 768px) {
    .main-intro__dino {
      position: relative;
      top: 0; } }
  .main-intro__column {
    flex: 1; }
  .main-intro__about {
    flex: 0 0 352px; }
    @media (max-width: 768px) {
      .main-intro__about {
        position: fixed;
        top: 0;
        bottom: 0;
        left: -1000px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10% 25%;
        opacity: 0;
        color: #ffffff;
        background-color: #2c39f2;
        overflow: hidden;
        transition: 0.6s cubic-bezier(0.55, 0, 0.1, 1); } }
  @media (max-width: 768px) {
    .main-intro__about--show {
      top: 0;
      right: 0;
      left: 0;
      opacity: 1; } }

.dino__path--red {
  color: #ff1553; }

.dino__path--violet {
  color: #9627a3; }

@media (max-width: 768px) {
  .dino__path {
    color: #ffffff; } }

.dino__path--blue {
  color: #2c39f2; }
  @media (max-width: 768px) {
    .dino__path--blue {
      color: #ffffff; } }

@media (max-width: 768px) {
  .dino {
    color: #000000; } }

@media (max-width: 768px) {
  .dino__flag {
    stroke-width: 1px;
    stroke: #000000; } }

.dino__letter {
  color: #2c39f2; }

.footer {
  min-height: 150px; }

